// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
import App from './App'
import Routes from './routers' //全局引入路由
import axios from 'axios'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)
Vue.use(mavonEditor)
/* eslint-disable no-new */
//全局自定义指令
// Vue.directive('rainbow',{
//   bind(el,binding,vnode){ //el代表元素,binding代表值
//     el.style.color = "#"+Math.random().toString(16).slice(2,8);
//   }
// })

//全局配置axios
axios.defaults.baseURL = 'https://project-7656072680893307727-default-rtdb.firebaseio.com'
// axios.defaults.headers.common['Authorization'] = 'Token'
// axios.defaults.headers.post['Content-type'] = 'application...urlencoded'


Vue.directive('theme',{
  bind(el,binding,vnode){
    if(binding.value == 'wide'){
      el.style.maxWidth = "1260px"
    }else if(binding.value == 'narrow'){
      el.style.maxWidth == "560px"
    }
  }
})

//全局自定义过滤器
Vue.filter('snippet',function(value){
  return value.slice(0,100) + '...'
})

//创建路由
const router = new VueRouter({
  routes:Routes,
  mode:"history"
})

new Vue({
  el: '#app',
  router:router,
  components: { App },
  template: '<App/>'

})



