<template>
  <div id="editor">
      <!--:ishljs为高亮-->
      <!--:v-model获取到编辑的 Markdown代码，需要用marked解析成html-->
      <!--change为更改事件，一更改触发-->
      <!--imgAdd为添加图片调用的函数-->
      <!--imgDel为删除图片调用的函数-->
    <mavon-editor
      ref="md"
      style="height: 100%"
      :ishljs="true"
      v-model="mavonValue"
      @change="chang"
      @imgAdd="imgAdd"
      @imgDel="imgDel"
    ></mavon-editor>
    <button @click="uploadImg">统一上传图片</button>
    <button @click.prevent="post">上传</button>
  </div>
</template>
    <script>
// 导入对应组件以及样式表
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
//将markdown语法解析成html的插件
import marked from "marked";
import axios from 'axios'
export default {
  name: "editor",
  components: {
    mavonEditor
    // 或 'mavon-editor': mavonEditor
  },
  data() {
    return {
      mavonValue: "",
      markedValue:'',
      imgList: []
    };
  },
  methods: {
     //第一个参数为为解码的markdown语法代码，第二个参数为解码后的html代码
     chang: function (val,render) {
      //解析成html
      console.log(marked(this.mavonValue));
      this.markedValue=marked(this.mavonValue)
      // console.log(this.markedValue);
    },
    // 绑定@imgAdd event
    imgAdd(pos, file) {
      // 缓存图片信息（当前还是本地图片状态）
      this.imgList[pos] = file;
      // console.log(pos)
      console.log("添加图片：" + this.imgList[pos]);
    },
    imgDel(pos) {
      delete this.imgList[pos];
      console.log("删除图片：" + this.imgList[pos]);
    },
    uploadImg() {
      // 第一步.将图片上传到服务器（一次性上传）.
      for (var i in this.imgList) {
        console.log("上传图片" + this.imgList[i]);
        //接口提供了一种表示表单数据的键值对的构造方式，经过它的数据可以使用
        //multipart/form-data
        var formdata = new FormData();
        formdata.append("file", this.imgList[i]);

        axios({
          url: "https://project-7656072680893307727-default-rtdb.firebaseio.com/img",
          method: "post",
          data: formdata,
          headers: { "Content-Type": "multipart/form-data" }
        }).then(res => {
          // 第二步.将返回的url替换到文本原位置![...](./0) -> ![...](url)
          /**
           * $vm 指为mavonEditor实例，可以通过如下两种方式获取
           * 1. 通过引入对象获取: `import {mavonEditor} from ...` 等方式引入后，`$vm`为`mavonEditor`
           * 2. 通过$refs获取: html声明ref : `<mavon-editor ref=md ></mavon-editor>，`$vm`为 `this.$refs.md`
           */
          console.log(res);
           //pos为在编辑框中对应的位置0。。。，第二个为上传后的url
            this.$refs.md.$img2Url(pos,'https://project-7656072680893307727-default-rtdb.firebaseio.com/img')
        });
      }
    }
  }
};
</script>
    <style>
#editor {
  margin: auto;
  width: 80%;
  height: 580px;
}
</style>



