<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <mavon-editor
      ref="md"
      style="height: 100%"
      :ishljs="true"
      v-model="blog.content"
      @change="change"
      @imgAdd="imgAdd"
      @imgDel="imgDel"
    ></mavon-editor>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>JavaScript</label>
        <input type="checkbox" value="JavaScript" v-model="blog.categories" />
        <label>Html/CSS</label>
        <input type="checkbox" value="Html/CSS" v-model="blog.categories" />
        <label>计网</label>
        <input
          type="checkbox"
          value="ComputerNetwork"
          v-model="blog.categories"
        />
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">{{ author }}</option>
      </select>
      <button @click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{ blog.title }}</p>
      <p>博客内容:</p>
      <p>{{ blog.content }}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories" :key="category">{{ category }}</li>
      </ul>
      <p>作者：{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
//将markdown语法解析成html的插件
import marked from "marked";
import axios from 'axios'
export default {
  //http://jsonplaceholder.typicode.com/posts
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        markedValue:"",
        categories: [],
        author: ""
      },
      authors: ["Apple", "Ringo", "Casey","Dongchen Yang"],
      submmited: false
    };
  },
  methods: {
    //第一个参数为为解码的markdown语法代码，第二个参数为解码后的html代码
     change: function (val,render) {
      //解析成html
      console.log(marked(this.blog.content));
      this.blog.markedValue=marked(this.blog.content)
      // console.log(this.markedValue);
    },
    // 绑定@imgAdd event
    imgAdd(pos, file) {
      // 缓存图片信息（当前还是本地图片状态）
      this.imgList[pos] = file;
      // console.log(pos)
      console.log("添加图片：" + this.imgList[pos]);
    },
    imgDel(pos) {
      delete this.imgList[pos];
      console.log("删除图片：" + this.imgList[pos]);
    },uploadImg() {
      // 第一步.将图片上传到服务器（一次性上传）.
      for (var i in this.imgList) {
        console.log("上传图片" + this.imgList[i]);
        //接口提供了一种表示表单数据的键值对的构造方式，经过它的数据可以使用
        //multipart/form-data
        var formdata = new FormData();
        formdata.append("file", this.imgList[i]);

        axios({
          url: "https://project-7656072680893307727-default-rtdb.firebaseio.com/img",
          method: "post",
          data: formdata,
          headers: { "Content-Type": "multipart/form-data" }
        }).then(res => {
          // 第二步.将返回的url替换到文本原位置![...](./0) -> ![...](url)
          /**
           * $vm 指为mavonEditor实例，可以通过如下两种方式获取
           * 1. 通过引入对象获取: `import {mavonEditor} from ...` 等方式引入后，`$vm`为`mavonEditor`
           * 2. 通过$refs获取: html声明ref : `<mavon-editor ref=md ></mavon-editor>，`$vm`为 `this.$refs.md`
           */
          console.log(res);
           //pos为在编辑框中对应的位置0。。。，第二个为上传后的url
            this.$refs.md.$img2Url(pos,url)
        });
      }
    },
    post: function() {
      //post请求将博客数据发送到数据库
    //   this.$http
        // .post("https://project-7656072680893307727-default-rtdb.firebaseio.com/posts.json",this.blog)
        axios.post("https://project-7656072680893307727-default-rtdb.firebaseio.com/posts.json",this.blog)
        .then(value => {//使用箭头函数将this指向上一层的this
          console.log(value);
          this.submmited = true;
        });
    }
  }
};
</script>

<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 6px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>



