<template>
  <div id="single-blog"  >
      <h1>{{blog.title}}</h1>
      <h1 class="markdown-body" v-html="blog.markedValue">{{blog.content}}</h1>
      <p>作者：{{blog.author}}</p>
      <ul>
          <li v-for="category in blog.categories" :key="category">
              {{category}}
          </li>
      </ul>
      <button @click="deleteSingleBlog()">删除</button>
      <router-link :to="'/edit/' + id">编辑</router-link>
  </div>
</template>

<script>

import axios from 'axios'
import "mavon-editor/dist/css/index.css"
export default {
  name: "single-blog",
  data() {
    return {
        id:this.$route.params.id,
        blog:{}
    };
  },
  created(){
    //   this.$http.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json")
      axios.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json")
        .then(data=>{
            console.log(data);
            // return data.json();
           this.blog = data.data //返回成功的回调

        })
        // .then(data=>{
        //     this.blog = data; //将获取到的data数据传递给组件
        // })
  },
  methods: {
      deleteSingleBlog(){
        //   this.$http.delete('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json")
          axios.delete('/posts/'+this.id+".json")
                    .then(res=>{
                        this.$router.push({path:'/'})
                    })
      }
  },
};
</script>

<style scoped>

    #single-blog{
        max-width: 960px;
        margin:0 auto;
        padding: 20px;
        background: #eee;
        border: 1px dotted #aaa;
    }
</style>



