<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>JavaScript</label>
        <input type="checkbox" value="JavaScript" v-model="blog.categories" />
        <label>Html/CSS</label>
        <input type="checkbox" value="Html/CSS" v-model="blog.categories" />
        <label>计网</label>
        <input
          type="checkbox"
          value="ComputerNetwork"
          v-model="blog.categories"
        />
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">{{ author }}</option>
      </select>
      <button @click.prevent="post">编辑博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题：{{ blog.title }}</p>
      <p>博客内容:</p>
      <p>{{ blog.content }}</p>
      <p>博客分类</p>
      <ul>
        <li v-for="category in blog.categories" :key="category">{{ category }}</li>
      </ul>
      <p>作者：{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  //http://jsonplaceholder.typicode.com/posts
  name: "add-blog",
  data() {
    return {
      id:this.$route.params.id,
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },
      authors: ["Apple", "Ringo", "Casey","Dongchen Yang"],
      submmited: false
    };
  },
  methods: {
    fetchData(){
            // console.log(this.id);
        // this.$http.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json")
        axios.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json")
                .then(res=>{
                    console.log(res);//获取请求
                    // console.log(res.body);//拿到对象
                    this.blog = res.data
                })
    },
    post:function(){
        // this.$http.put('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts/'+this.id+".json",this.blog)
        axios.put('/posts/'+this.id+".json",this.blog)
        .then(value => {
          console.log(value);
          this.submmited = true;
        });
    }
  },
  created(){
      this.fetchData();
  }
};
</script>

<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}
#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 6px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>



