<template>
  <div v-theme="'wide'" id="show-blogs"> <!-- 只用""不行，因为这样存出来wide是一个变量 -->
      <h1>博客总览</h1>
      <input type="text" v-model="search" placeholder="搜索">
      <div  v-for="blog in filteredBlogs" class = "single-blog" :key="blog.id">
        <router-link :to="'/blog/'+blog.id"><!-- 不加v-bind的话，不能动态绑定数据，将数据库换成自己的之后,没有blog.id属性，得自己想办法 -->
            <h2 v-rainbow>{{blog.title | to-uppercase}}</h2><!-- 自定义filter -->
        </router-link>
        <!-- <div class="markdown-body" v-html="blogs"> -->
          <article>{{blog.content | snippet}}</article>
          <!-- </div> -->
          
      </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'show-blogs',
    data(){
        return{
            blogs:[], //将created里面获取到的blogs数据返回
            search:"",
        }
    },
    created(){
        // this.$http.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts.json')
        axios.get('https://project-7656072680893307727-default-rtdb.firebaseio.com/posts.json')
        .then(data =>{
            // console.log(data.json());// data.json()是一个promise对象
            // return data.json()
            // this.blogs = data.body.slice(0,10);//将json.placeholder中的数据换成自己数据库中的数据后，data.body不是数组了而是对象
            //解决办法：将每个对象放进数组中，将对象的name值设置为id
            // console.log(this.blogs);
            return data.data //axios不需要 data.json()解析，axios返回的对象中有data属性
        })
        .then(data=>{
            var blogsArray = [];
            for(let key in data){
                // console.log(key); //打印出来的就是name
                // console.log(data[key]);//打印出来的是每一个对象
                data[key].id = key;
                blogsArray.push(data[key])
            }
            // console.log(blogsArray);
            this.blogs = blogsArray //将从数据库获取的对象数组传递给组件中的blogs对象
            console.log(this.blogs);
        })
    },
    computed:{//搜索功能，涉及到属性的计算
        filteredBlogs:function(){
            return this.blogs.filter((blog)=>{
                return blog.title.match(this.search)
            })
        }
    },
    filters:{ //局部定义filter
        "to-uppercase":function(value){
            return value.toUpperCase();
        }
    },
    directives:{//局部定义directive
        'rainbow':{
            bind(el,binding,vnode){
                el.style.color = "#"+Math.random().toString(16).slice(2,8);
            }
        }
    }
}
</script>

<style scoped>
    #show-blogs{
        max-width: 800px;
        margin:0 auto;
    }

    .single-blog{
        padding:20px;
        margin:20px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px dotted #aaa;
    }

    #show-blogs a{
        color: #444;
        text-decoration: none;
    }

    input[type="text"]{
        padding: 8px;
        width: 100%;
        box-sizing: border-box;
    }
</style>



